<template >
  <div class="first">
    <!-- 顶部导航栏 -->
    <div class="navigation">
      <div class="returnClick" @click="onClickReturn">
        <img
          class="returnIcon"
          src="data:image/png;base64,"
          alt
        />
      </div>
      <van-nav-bar title="地址管理" fixed @click-right="onClickSave" />
    </div>
    <!-- 中部地址栏 -->
    <div class="view-main">

       <van-radio-group v-model="radio" checked-color="#c53539">
      <div class="address-list-item" v-for="(item,index) in addressList" :key="index">
        <div class="info" @click="goComirm">
          <div class="addressee">{{item.name}}&nbsp;&nbsp;{{item.tel}}</div>
          <div class="address-detail">{{item.addressDetail}}</div>
        </div>
        <div class="operation">
          <!-- <div class="isDefault">
                        <van-checkbox v-model="item.checked" checked-color="#C71D23" @click="selectOne(index)"> <span>默认地址</span> </van-checkbox>
          </div>-->

         
            <van-radio :name="index" @click="selectOne(item,item.addressId)">默认地址</van-radio>          

          <div class="delEdit">
            <div class="delete" @click="delButton(item.addressId,index)">
              <img
                src="data:image/png;base64,"
                alt
              />
              <span>删除</span>
            </div>
            <div class="edit" @click="editButton(index)">
              <img
                src="data:image/png;base64,"
                alt
              />
              <span>编辑</span>
            </div>
          </div>
        </div>
      </div>
      </van-radio-group>
    </div>
    <div class="kongbai"></div>
    <!-- 底部新建栏 -->
    <div class="address-list-button">
      <div>
        <img
          src="data:image/png;base64,"
          alt
        />
        <router-link to="/address/area" tag="span">新建地址</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // checked: "",
      addressList: [],
      radio: 1
    };
  },
  computed: {
    address() {
      return this.$store.state.address;
    }
  },
  created() {
    this.getAddress();
  },
  methods: {
    goComirm(){
         this.$router.push("/order/confirm");
    },
    //选择默认地址
    selectOne(data,index) {
      console.log("默认地址：",index,"data",data)
       let url ="/address/edit?addressId="+index
     
     let deta={
       ...data,
       isDefault:true
     }
       this.$axios.post(url,deta).then(res=>{
         console.log("默认修改成功：",res)
       }).catch(err=>{
         console.log("默认修改失败：",err)
       })
      // setTimeout(() => {
      //   this.$router.push("/order/confirm");
      // }, 1000);
    },

    //获取地址的数据列表
    getAddress() {
      let url = "/address/all";
      this.$axios
        .post(url)
        .then(res => {
          console.log("获取地址的数据", res);
          // this.addressList=res.list
          //为数组的每一个对象添加选中的状态值
          this.addressList = res.list.map(item => {
            return {
              ...item,
              checked: false
            };
          });

         res.list.forEach((i,ix) => {
           if(i.isDefault){
             this.radio=ix
           }
         });



        })
        .catch(err => {
          console.log("获取地址失败：", err);
        });
    },
    //   导航栏返回事件
    onClickReturn() {
      this.$router.push("/order/confirm");
    },
    //删除按钮事件
    delButton(addressId, index) {
      ///////////////////
       
          let data = {
        addressId: addressId
      };
       this.$dialog.confirm({
            title: '提示',
            message: '退出删除'
            }).then(() => {
           let url = "/address/del";
                this.$axios.post(url,data).then(res=>{
                   let clear= this.$toast.success("删除成功")
                    setTimeout(()=>{
                        clear.clear()                        
                    },500)
                     this.addressList.splice(index, 1);                  
                })
           
            
            }).catch(() => {
          
            });   

        ////////////////删除结束
    },
    //编辑按钮事件
    editButton(index) {
      console.log("编辑", this.addressList[index]);
      this.$store.commit("editaddress", this.addressList[index]);
      this.$router.push(`/address/edit/${this.addressList[index].addressId}`);
      // this.$router.push(`/address/edit`);
    },
    onClickSave() {}
  }
};
</script>

<style lang="less">
.van-checkbox__label {
  color: #797d82;
}
</style>

<style lang="less" scoped>
// body的样式
.first {
  background: #f4f4f4;
  width: 100vw;
  min-height: 100vh;
}
// 导航栏
.navigation {
  height: 44px;
  width: 100vw;
  display: flex;
  position: fixed;
  z-index: 100;
}
.van-hairline--bottom {
  height: 44px;
}
// 返回按钮
.returnClick {
  display: flex;
  height: 100%;
  width: 41px;
  box-sizing: border-box;
  align-items: center;
}
// 返回图标
.returnIcon {
  margin-left: 14px;
  vertical-align: middle;
  width: 11px;
  height: 19px;
  font-size: 15px;
  color: #191a1b;
}
// 顶部导航栏地址管理样式
.van-nav-bar__title {
  font-size: 17px;
  font-family: 600;
}

// 顶部导航栏左边返回号样式
.returnClick {
  z-index: 999;
  position: relative;
}
.van-icon-arrow-left {
  color: black;
  height: 100%;
  font-size: 20px;
  left: 5px;
}
// 中部地址栏
.view-main {
  position: relative;
  top: 44px;
  bottom: 50px;
  overflow-y: scroll;
  width: 100%;
  box-sizing: border-box;
  .address-list-item {
    background-color: #fff;
    margin-bottom: 10px;
    box-sizing: border-box;
    .info {
      padding: 15px;
      box-sizing: border-box;
      border-bottom: #f4f4f4 1px solid;
      .addressee {
        font-size: 15px;
        height: 22px;
        box-sizing: border-box;
      }
      .address-detail {
        margin: 2px 0 0;
        font-size: 13px;
        color: #797d82;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        max-height: 50px;
        box-sizing: border-box;
        height: 19px;
      }
    }
    .operation {
      padding: 10px;
      color: #797d82;
      font-size: 13px;
      position: relative;
      display: flex;
      justify-content: space-between;
      .isDefault {
        display: inline-block;
        height: 18px;
        line-height: 18px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        .van-checkbox {
          padding-left: 5px;
        }
        span {
          vertical-align: middle;
          line-height: 18px;
          font-size: 13px;
          color: #797d82;
        }
      }
    }
    .delete {
      margin-right: 5px;
      float: right;
      box-sizing: border-box;
      color: #797d82;
      font-size: 13px;
      padding-right: 10px;

      img {
        width: 17px;
        height: 17px;
        vertical-align: middle;
        color: #797d82;
        font-size: 13px;
      }
      span {
        vertical-align: middle;
        color: #797d82;
        font-size: 13px;
        padding-left: 3px;
      }
    }
    .edit {
      float: right;
      margin-right: 15px;
      box-sizing: border-box;
      color: #797d82;
      font-size: 13px;
      img {
        width: 17px;
        height: 17px;
        vertical-align: middle;
        color: #797d82;
        font-size: 13px;
      }
      span {
        vertical-align: middle;
        color: #797d82;
        font-size: 13px;
        padding-left: 3px;
      }
    }
  }
}
// 底部新建栏
.address-list-button {
  position: absolute;
  bottom: 0;
  background-color: #c53539;
  z-index: 100;
  height: 50px;
  position: fixed;
  width: 100%;
  font-size: 16px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;

  div {
    height: 21px;
    display: flex;
    span {
      font-size: 17px;
      color: #ffffff;
    }
  }
  img {
    font-size: 16px;
    height: 17px;
    width: 17px;
    margin-right: 15px;
    background-color: #c53539;
    color: #ffffff;
    border-radius: 50%;
  }
}
//空白
.kongbai{
  height: 80px;
  width: 100%;
  background: #fff;
}
</style>